import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ModalController } from '@ionic/angular';
import {ConfigService} from './../config.service'
@Component({
  selector: 'app-ventas',
  templateUrl: './ventas.page.html',
  styleUrls: ['./ventas.page.scss'],
})
export class VentasPage implements OnInit {
  public items;
  public cant;
  public fecha;
  public pedidos
  constructor(private host:ConfigService,private http: HttpClient,private modal: ModalController) { 
    this.http.get<any>(this.host.getHost() + '/selectElabora').subscribe(data => {
      this.items = data;
    })
    this.pedidos=[]
    this.cant = 0;
    this.fecha=Date();
  }
  OnInit(){
    
  }
  addItem(item){

    item.cant = this.cant
    item.Fecha =this.fecha
    this.pedidos.push(item)
  }
  generarVenta(){
    console.log(this.pedidos)

    this.http.post<any>(this.host.getHost() + '/insertVentas',{produc:this.pedidos}).subscribe(data => {

    });
    this.navBack();
  }
  navBack(){

    this.modal.dismiss({
      'dismissed': true
    })
  }
  ngOnInit() {
  }

}
