import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {NavParams} from '@ionic/angular';
import { ModalController } from '@ionic/angular';
import { FormsModule } from '@angular/forms';
import {ConfigService} from './../config.service'
@Component({
  selector: 'app-detail-prima',
  templateUrl: './detail-prima.page.html',
  styleUrls: ['./detail-prima.page.scss'],
})
export class DetailPrimaPage implements OnInit {
  public items;
  public selectItem ;
  public Nombre = '';
  pushPage: any;
  constructor(private host:ConfigService, private http: HttpClient,private params:NavParams,private modal: ModalController,private form:FormsModule) { 
    if(params.data.items!=null){
      this.selectItem = params.data.items;
      this.http.post<any>(this.host.getHost() + '/historycost',this.selectItem).subscribe(data => {
        this.items = data;
      })
    }else{
      this.selectItem = {Id:null,Nombre:"",Precio:0,Unidad:"U"};
    }
   
  }
  navBack(){
    
    this.modal.dismiss()
  }
  save(){
    if(this.selectItem.Id!=null)
    {
      this.http.post(this.host.getHost() + '/UpdatePrima',this.selectItem).subscribe(data => {
        console.log(data['_body']);
      }, error => {
          console.log(error);
      });
    }else{
      this.http.post(this.host.getHost() + '/InsertPrima',this.selectItem).subscribe(data => {
        console.log(data['_body']);
      }, error => {
          console.log(error);
      });
    }
    
    this.navBack();
  }
  ngOnInit() {
  }

}
