import { Pipe, PipeTransform } from '@angular/core';

import { material } from './material';

@Pipe({
    name: 'materialfilter',
    pure: false
})
export class materialFilterPipe implements PipeTransform {
  transform(items: material[], filter: material): material[] {
    if (!items || !filter) {
      return items;
    }
    // filter items array, items which match and return true will be kept, false will be filtered out
    return items.filter((item: material) => this.applyFilter(item, filter));
  }
  
  /**
   * Perform the filtering.
   * 
   * @param {material} material The material to compare to the filter.
   * @param {material} filter The filter to apply.
   * @return {boolean} True if material satisfies filters, false if not.
   */
  applyFilter(material: material, filter: material): boolean {
    for (let field in filter) {
      if (filter[field]) {
        if (typeof filter[field] === 'string') {
          if (material[field].toLowerCase().indexOf(filter[field].toLowerCase()) === -1) {
            return false;
          }
        } else if (typeof filter[field] === 'number') {
          if (material[field] !== filter[field]) {
            return false;
          }
        }
      }
    }
    return true;
  }
}