import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {NavParams} from '@ionic/angular';
import { ModalController } from '@ionic/angular';
import { FormsModule } from '@angular/forms';
import {ConfigService} from './../config.service'
@Component({
  selector: 'app-detailalmacen',
  templateUrl: './detailalmacen.page.html',
  styleUrls: ['./detailalmacen.page.scss'],
})
export class DetailalmacenPage {
  public items;
  public selectItem ;
  constructor(private host:ConfigService,private http: HttpClient,private params:NavParams,private modal: ModalController,private form:FormsModule) { 
    if(params.data.items!=null){
      this.selectItem = params.data.items;
      this.selectItem.cant = 1;
      this.selectItem.Fecha = Date();
    }
    this.detailAlmacen()
  }
  navBack(){
    
    this.modal.dismiss()
  }
  insertAlmacen(){
    this.http.post<any>(this.host.getHost() + '/insertAlmacen',this.selectItem).subscribe(data => {
      this.detailAlmacen()
      this.disponibilidadAlmacen();
    })
  }
  detailAlmacen(){
    this.http.post<any>(this.host.getHost() + '/detailAlmacen',this.selectItem).subscribe(data => {
      this.items = data;
    })
  }
  disponibilidadAlmacen(){
    this.http.get<any>(this.host.getHost() + '/DisponibilidadAlmacen').subscribe(data => {
      
      data.forEach(element => {
        if (element.Id == this.selectItem.Id ){
          this.selectItem = element
          this.selectItem.cant = 0;
          this.selectItem.Fecha = Date();
        }
        
      });
     

    })
  }
}
